/* src/app/shared/components/navbar/navbar.component.css */
.navbar {
  position: sticky;
  top: 0;
  width: 100%;
  background:
    linear-gradient(
      135deg,
      rgba(12, 20, 32, 0.92) 0%,
      rgba(18, 32, 50, 0.92) 60%) !important;
  -webkit-backdrop-filter: blur(14px) saturate(140%);
  backdrop-filter: blur(14px) saturate(140%);
  z-index: 1000;
  transition: padding .35s, box-shadow .35s;
  padding: .9rem 0;
  border-bottom: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 4px 18px -6px rgba(0, 0, 0, 0.5);
}
.navbar.scrolled {
  padding: .55rem 0;
  box-shadow: 0 6px 28px -8px rgba(0, 0, 0, 0.6);
}
.nav-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.logo {
  display: flex;
  align-items: center;
  gap: .5rem;
  color: #fff;
  text-decoration: none;
  font-weight: 600;
  margin-left: 0;
}
.logo img {
  height: 55px;
  width: auto;
  display: block;
  filter: brightness(1.25) contrast(1.2) saturate(1.15) drop-shadow(0 2px 8px rgba(0, 0, 0, 0.6));
  object-fit: contain;
  transition: transform 0.3s ease, filter 0.3s ease;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  backface-visibility: hidden;
  -webkit-font-smoothing: antialiased;
}
.logo:hover img {
  transform: scale(1.05);
  filter: brightness(1.35) contrast(1.3) saturate(1.2) drop-shadow(0 3px 10px rgba(11, 110, 255, 0.4));
}
.logo img[style*="display:none"] + .wordmark {
  padding-left: 0;
}
.wordmark {
  font-size: 1.1rem;
  letter-spacing: .5px;
}
.nav-links {
  list-style: none;
  display: flex;
  margin: -100;
  padding: 0;
  align-items: center;
  gap: 2rem;
}
.nav-links li {
  position: relative;
}
.nav-links li.dropdown > a {
  display: flex;
  align-items: center;
  gap: 0.4rem;
}
.dropdown-icon {
  transition: transform 0.2s ease;
}
.nav-links li.dropdown > a.active .dropdown-icon {
  transform: rotate(180deg);
}
.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  margin-top: 0.5rem;
  list-style: none;
  padding: 0.5rem 0;
  background: rgba(12, 20, 32, 0.95);
  -webkit-backdrop-filter: blur(14px) saturate(140%);
  backdrop-filter: blur(14px) saturate(140%);
  border: 1px solid rgba(255, 255, 255, 0.12);
  border-radius: 8px;
  box-shadow: 0 8px 24px rgba(0, 0, 0, 0.4);
  min-width: 180px;
  z-index: 1001;
  animation: dropdownFadeIn 0.2s ease;
}
@keyframes dropdownFadeIn {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.dropdown-menu li {
  margin: 0;
}
.dropdown-menu li a {
  display: block;
  padding: 0.65rem 1.2rem;
  color: #fff;
  text-decoration: none;
  transition: background 0.2s, color 0.2s;
  border-radius: 0;
  white-space: nowrap;
}
.dropdown-menu li a:hover {
  background: #0B6EFF;
  color: #fff;
}
.nav-links li a {
  letter-spacing: .4px;
}
.nav-inner.container {
  gap: 2.5rem;
}
.nav-links li a,
.nav-links li button {
  background: none;
  border: none;
  color: #fff;
  text-decoration: none;
  cursor: pointer;
  font: inherit;
  padding: .25rem .5rem;
  border-radius: 4px;
  transition: background .2s, color .2s;
  font-size: 18px;
}
.nav-links li a:hover,
.nav-links li button:hover {
  background: #0B6EFF;
}
.theme-toggle button {
  display: flex;
  align-items: center;
  gap: .4rem;
  font-weight: 600;
  font-size: .8rem;
  padding: .35rem .7rem;
  background: rgba(255, 255, 255, 0.08);
  border: 1px solid rgba(255, 255, 255, 0.15);
}
.theme-toggle button[data-theme=light] {
  background: #ffffff;
  color: #1f2933;
  border-color: #d1d9e2;
}
.theme-toggle button[data-theme=light]:hover {
  background: #f1f5f9;
}
.theme-toggle button[data-theme=dark] {
  background: rgba(255, 255, 255, 0.08);
  color: #fff;
}
.theme-toggle button[data-theme=dark]:hover {
  background: #0B6EFF;
  color: #fff;
}
.nav-links li.admin a {
  color: #00C28A;
}
.hamburger {
  display: none;
  flex-direction: column;
  gap: 4px;
  background: none;
  border: none;
  cursor: pointer;
}
.hamburger span {
  width: 28px;
  height: 3px;
  background: #fff;
  border-radius: 2px;
}
.auth-links button {
  background: #0B6EFF;
  color: #fff;
}
.auth-links button.signup-btn {
  background: #00C28A;
}
.avatar {
  background: #0B6EFF;
  color: #fff;
  border-radius: 50%;
  width: 40px;
  height: 40px;
}
@media (max-width: 900px) {
  .navbar {
    padding: 1rem 1.25rem;
  }
  .logo img {
    height: 48px;
  }
  .hamburger {
    display: flex;
    padding: 0.5rem;
    margin-right: -0.5rem;
    z-index: 1001;
  }
  .nav-links {
    position: fixed;
    inset: 0 0 0 25%;
    background:
      linear-gradient(
        135deg,
        #0f1724 0%,
        #1a2332 100%);
    flex-direction: column;
    padding: 5rem 2rem 2rem;
    transform: translateX(100%);
    transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    align-items: flex-start;
    gap: 0.5rem;
    box-shadow: -4px 0 24px rgba(0, 0, 0, 0.3);
    overflow-y: auto;
    z-index: 1000;
  }
  .nav-links.open {
    transform: translateX(0);
  }
  .nav-links a {
    width: 100%;
    padding: 0.875rem 1rem;
    border-radius: 8px;
    transition: all 0.2s ease;
  }
  .nav-links a:hover {
    background: rgba(255, 255, 255, 0.1);
    padding-left: 1.25rem;
  }
  .dropdown-menu {
    position: static;
    margin-top: 0.5rem;
    margin-left: 1rem;
    box-shadow: none;
    border: none;
    background: rgba(255, 255, 255, 0.05);
    border-radius: 8px;
    padding: 0.5rem 0;
    background: rgba(255, 255, 255, 0.05);
    animation: none;
  }
  .dropdown-menu li a {
    padding: 0.5rem 1rem;
    font-size: 0.9rem;
  }
  .nav-links li.dropdown > a {
    width: 100%;
    justify-content: space-between;
  }
  .auth-links {
    width: 100%;
    flex-direction: column;
    gap: 0.75rem;
    margin-top: 1rem;
    padding-top: 1rem;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
  }
  .auth-links button {
    width: 100%;
    padding: 0.875rem 1.5rem;
    min-height: 48px;
    font-size: 1rem;
  }
}
@media (max-width: 600px) {
  .navbar {
    padding: 1rem 1.25rem;
  }
  .nav-links {
    inset: 0;
    padding: 4.5rem 1.5rem 2rem;
  }
  .logo {
    font-size: 1.375rem;
  }
}
@media (max-width: 480px) {
  .navbar {
    padding: 0.875rem 1rem;
  }
  .logo img {
    height: 42px;
  }
  .nav-links {
    padding: 4rem 1.25rem 2rem;
  }
  .logo {
    font-size: 1.25rem;
  }
  .hamburger span {
    width: 24px;
  }
}
/*# sourceMappingURL=navbar.component.css.map */
